.aiTrainer {
  display: flex;
  flex-direction: column;
  padding: 24px;
}

@media (min-width: 992px) {
  .aiTrainer {
    padding: 40px;
  }
}

.content {
  display: grid;
  grid-gap: 24px 40px;
  grid-template:
    "title" auto
    "description" auto
    "leftBox" auto
    "rightBox" auto / 100%;
}

.title {
  grid-area: title;
  font-size: 24px;
  text-transform: uppercase;
}

.description {
  grid-area: description;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  overflow: hidden;
}

.image {
  flex-shrink: 0;
  margin: 0 -72px;
  width: 400px;
  height: 232px;
}

%box {
  padding: 8px;
  background-color: white;
  border-radius: 4px;
  box-shadow: 0px 3px 6px #00000029;
}

[dark-theme] {
  %box {
    background-color: #242b4384;
    box-shadow: none;
  }
}

.leftBox {
  grid-area: leftBox;
  @extend %box;
}

.rightBox {
  grid-area: rightBox;
  @extend %box;
}

.boxTitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
}

.dimmedText {
  opacity: 0.75;
}

.wordTilesWrapper {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  max-height: 280px;
  overflow: auto;
}

%wordTile {
  margin: 8px;
}

.wordTile {
  &LeftBox {
    @extend %wordTile;
    width: calc(50% - 16px);
  }
  &RightBox {
    @extend %wordTile;
    width: calc((100% - 32px) / 2);
    border-color: #ef336a;
  }
}

.placeholder {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  min-height: 56px;
  font-size: 14px;
  opacity: 0.75;
}

.button {
  &Close {
    align-self: flex-end;
    display: flex;
    align-items: center;
    font-size: 13px;
  }

  &Arrow {
    margin-left: 8px;
    padding: 0;
    opacity: 0.5;
    transition: 0.2s;
    &:hover {
      opacity: 1;
    }
  }

  &Recalculate {
    align-self: center;
    margin-top: 24px;
    height: 40px;
    padding: 0 32px;
    font-size: 16px;
    color: #f7f7f7;
    background-color: #41b167;
    border-radius: 4px;
  }
}

@media (min-width: 768px) {
  .content {
    grid-template:
      "title title" auto
      "description description" auto
      "leftBox rightBox" auto / 1fr 1fr;
  }
  .wordTilesWrapper {
    height: 280px;
  }
  .button {
    &Recalculate {
      width: initial;
    }
  }
}

@media (min-width: 992px) {
  .description {
    flex-direction: row;
  }
}

@media (min-width: 1200px) {
  .content {
    grid-template:
      "title description" auto
      "leftBox rightBox" auto / calc((100% - 40px - 48px - 96px) / 7 * 2 + 48px)
      calc((100% - 40px - 48px - 96px) / 7 * 5 + 96px);
  }
  .wordTile {
    &RightBox {
      width: calc((100% - 80px) / 5);
    }
  }
}
